.container{
  position: relative;
  top: 0;
  left: 0;
  width: 250px;
  height: 350px;
}

.photo{
  width: 100%;
  height: 100%;
  object-fit: color;
  object-position: center;
  border-radius: var(--border-radius-medium);
}

.favorite{
  position: absolute;
  top: -15px;
  right: -15px;
  width: 35px;
  height: 35px;
  cursor: pointer;
  object-fit: cover;
  object-position: center center;
  filter: drop-shadow(0px 0px 2px #000);
}