.container {
  --border-radius: 10px;

  --color-light: #29e52d;
  --color-dark: #d82228;
  --color-neutral: #ffff00;

  --color-shadow-light: rgba(41, 229, 45, .4);
  --color-shadow-dark: rgba(216, 34, 40, .4);
  --color-shadow-neutral: rgba(224, 224, 81, .4);
}

.container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 40px;
}

.item {
  position: relative;
  top: 0;
  left: 0;
  width: 250px;
  height: 450px;
  margin-right: var(--spacing-medium);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow-black);
  transition: var(--transition);
  cursor: pointer;
}

.item__header {
  position: absolute;
  bottom: 20px;
  width: 100%;
  font-size: var(--font-size-subheader);
  text-align: center;
}

.item__img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center center;
  border-radius: var(--border-radius);
}


/* COLOR THEMES */

.item__light {
  text-shadow: 0 0 5px currentColor;
  color: var(--color-light);
}

.item__dark {
    text-shadow: 0 0 5px currentColor;
      color: var(--color-dark);
}

.item__neutral {
    text-shadow: 0 0 5px currentColor;
      color: var(--color-neutral);
}


.item__light:hover {
box-shadow: 0 0 7px 2px var(--color-shadow-light)
}

.item__dark:hover {
box-shadow: 0 0 7px 2px var(--color-shadow-dark)
}

.item__neutral:hover {
box-shadow: 0 0 7px 2px var(--color-shadow-neutral)
}