.link{
  display: inline-flex;
  align-items: center;
  margin-top: 15px;
  margin-bottom: var(--spacing-large);
  font-weight: var(--font-bold);
  color: var(--color-yellow);
  text-decoration: none;
}

.link__img{
  width: 30px;
  height: 30px;
  margin-right: var(--spacing-small);
  object-position: center center;
  object-fit: cover;
}